body {
  display: flex;
  flex-direction: column;

  font-family: var(--font-main);
  font-size: 16px;
  overflow-x: hidden;
  /* transition: background-color 1000ms cubic-bezier(0.075, 0.82, 0.165, 1); */
}
main {
  flex-grow: 1;
}

/* // скидання налаштувань браузера */

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.margin-top .support {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
p,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding-left: 0;
  list-style: none;
}

/* // базові параметри зображень */
img {
  display: block;
  width: 100%;
  border-radius: 8px;
}

/* // приховування тексту */
.hiden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* 
// налаштування контейнерів */
@media screen and (min-width: 375px) {
  .container {
    display: flex;
    flex-direction: row;
    outline: 1px solid red;
    max-width: 375px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    outline: 1px solid red;
    max-width: 1279px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    outline: 1px solid red;
    max-width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }
}
